<template>
    <div class="home-list-page">
        <!-- 头 -->
		<van-nav-bar title="点位猜涨跌记录" fixed left-arrow :border="false" @click-left="goBack"/>
        <div class="list-box">
            <van-list v-if="list.length > 0" v-model="loading" :finished="finished" finished-text="没有更多了" @load="getList">
                <div class="list-item" v-for="(item,index) in list" :key="index">
                    <div class="list-item-title">
                        <div>BTC</div>
                        <div class="list-item-title-type" :class="{'list-item-title-type-down':item.style == 2}">
                            
                            <span v-if="item.style == 1">看涨</span>
                            <span v-if="item.style == 2">看跌</span>
                            （{{item.usdt_fee}}USDT）
                        </div>
                        <div class="list-item-title-result" :class="{'list-item-title-result-fail':item.is_win == 2}">
                            <span v-if="item.is_win == 1">盈</span>
                            <span v-if="item.is_win == 2">亏</span>
                        </div>
                        <div class="list-item-title-result" :class="{'list-item-title-result-fail':item.is_win == 2}">
                            <span v-if="item.is_win == 1">+{{item.win_price}}</span>
                            <span v-if="item.is_win == 2">-{{item.usdt_fee}}</span>
                        </div>
                    </div>
                    <div class="list-item-price">
                        <div class='list-item-price-item'>
                            <span class='list-item-price-item-name'>下单价</span>
                            <span class='list-item-price-item-value'>{{item.buy_price | toFixedNum(2)}}</span>
                        </div>
                        <div class='list-item-price-item'>
                            <span class='list-item-price-item-name'>结算价</span>
                            <span class='list-item-price-item-value'>{{item.rise_price | toFixedNum(2)}}</span>
                        </div>
                    </div>
                    <div class="list-item-price">
                        <div class='list-item-price-item'>
                            <span class='list-item-price-item-name'>止盈价</span>
                            <span class='list-item-price-item-value'>{{item.rise_price | toFixedNum(2)}}</span>
                        </div>
                        <div class='list-item-price-item'>
                            <span class='list-item-price-item-name'>止损价</span>
                            <span class='list-item-price-item-value'>{{item.fill_price | toFixedNum(2)}}</span>
                        </div>
                    </div>
                    <div class="list-item-price">
                        <div class='list-item-price-item'>
                            <span class='list-item-price-item-name'>竞猜数量</span>
                            <span class='list-item-price-item-value'>{{item.buy_quiz}}手</span>
                        </div>
                        <div class='list-item-price-item'>
                            <span class='list-item-price-item-name'>手续费</span>
                            <span class='list-item-price-item-value'>{{item.sx_fee}}USDT</span>
                        </div>
                    </div>
                    <div class="list-item-time">下单时间：{{item.add_time}}</div>
                    <div class="list-item-time">结算时间：{{item.end_time}}</div>
                </div>
            </van-list>

            <div class="data-none" v-if="list.length == 0">
                暂无记录
            </div>
        </div>
    </div>
</template>


<script>

import { mapState } from 'vuex'

export default {
    data(){
        return{
            list:[],
            loading: false,
            finished: true,
            page:1,
        }
    },
    computed:{
		...mapState(['user_info']),//用户信息
    },
    filters:{
		//保留两位小数
		toFixedNum(val,num){
			if(val){
				return parseFloat(val).toFixed(num);
			}else{
				return ''
			}
			return val
		}
	},
    created(){
        //获取历史记录
        this.getList();
    },
    methods:{
        //返回
        goBack(){
            this.$router.go(-1);
        },
        //获取历史记录
        getList(){
            let option = {
                type:'2',
                token:this.user_info.token,
                page:this.page
            }
            this.http.post(this.http._HOST + 'wave/history_order',option).then(result =>{
                this.utils.info('获取历史记录',result);
                if(result.success){
                    let data = result.data;
                    
                    if(data.length == 0){
                        this.finished = true;    
                    }else{
                        this.finished = false;

                        if(this.page == 1){
                            this.list = result.data;    
                        }else{
                            for(let i=0;i< data.length;i++){
                                this.list.push(data[i]);
                            }
                        }

                        this.page++;
                    }

                    
                    this.loading = false;
                    
                }else{
                    this.utils.err('获取历史记录...3s后再次请求');
					let getListTimeout = setTimeout(() =>{
						this.getList();
						clearTimeout(getListTimeout);
					},3000)	
                }
            })
        }
    }
}
</script>


<style lang="scss" scoped>
    .home-list-page{
        width:100%;
        height:100%;
        background: #f3f3f3;
        color:#353535;
    }
	.van-nav-bar__title{
		font-size:18px;
		color:#353535;
	}
	.van-nav-bar .van-icon{
		font-size:20px;
		color:#353535;
	}
    .van-list__finished-text{
        background: #f3f3f3;   
    }
    
    //列表
    .list-box{
        padding-top:56px;
    }
    .list-item{
        background: #fff;
        padding:15px;
        margin-bottom: 10px;
    }
    .list-item-title{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size:16px;
        padding-bottom: 15px;
    }
    .list-item-title-type{
        color:#48be7b;
    }
    .list-item-title-type-down{
        color:#ff4a4a;
    }
    .list-item-title-result{
        color:#48be7b;
    }
    .list-item-title-result-fail{
        color:#ff4a4a;
    }

    .list-item-price{
		display: flex;
		align-items: center;
		justify-content: space-between;	
		padding-bottom: 5px;
	}
	.list-item-price:last-child{
		padding-bottom: 0;
	}
	.list-item-price-item{
		width:48%;
		display: flex;
		align-items: center;
	}
	.list-item-price-item-name{
		font-size:14px;
		color:#A4A5AF;
		padding-right:10px;
	}
	.list-item-price-item-value{
		font-size:14px;
		color:#353535;	
	}
    .list-item-time{
        font-size:12px;
        padding-top:5px;
    }
    .data-none{
        font-size:14px;
        text-align: center;
        color:#999;
        line-height:60px;
    }
	
</style>


